import React, { Component } from 'react';
import Navigation from '../components/navigation/Navigation';
import Footer from '../components/navigation/Footer';
import Logo from '../components/Logo/Logo';
import ImageLinkForm from '../components/ImageLinkForm/ImageLinkForm';
// import Main from '../components/Main/Main';
// import { List } from './List';
// import logo from './logo.svg';
import './App.css';
import recognizeMic from 'watson-speech/speech-to-text/recognize-microphone';
import Main from '../components/Main/Main';

class App extends Component {
  constructor() {
    super()
    this.state = {}
  }
  onlistenClick() {

    fetch('http://localhost:3002/api/speech-to-text/token')
      .then(function(response) {
          return response.text();
      }).then( (token) => {
        
        console.log('token is', token)

        var stream = recognizeMic({
            token: token,
            objectMode: true, // send objects instead of text
            extractResults: true, // convert {results: [{alternatives:[...]}], result_index: 0} to {alternatives: [...], index: 0}
            format: false // optional - performs basic formatting on the results such as capitals an periods
        });
        stream.on('data', (data) => {
          this.setState({ 
            text: data.alternatives[0].transcript
          })
        });
        stream.on('error', function(err) {
            console.log(err);
        });
        document.querySelector('#stop').onclick = stream.stop.bind(stream);
      }).catch(function(error) {
          console.log(error);
      });

  }
  
  render() {
    return (
      <div className="App">
          <div className="still-clouds section-dark"><img style={{paddingTop: ''}} alt='' url={('https://cdn.rawgit.com/creativetimofficial/paper-kit/bootstrap4-development/assets/img/antoine-barres.jpg')}/>
            <div className="content-center">
              <header className="App-header">
                <Navigation />
                <Logo />
                <h1 className="App-title">Welcome to React</h1>
                <div className="fogloe"></div>
              </header>
                <ImageLinkForm />
                <button onClick={this.onlistenClick.bind(this)}>Listen to microphone</button>
                <div>{this.state.text}</div>
                <div className="moving-clouds"><img alt='' url={('https://cdn.rawgit.com/creativetimofficial/paper-kit/bootstrap4-development/assets/img/clouds.png')}/></div>
            </div>    
          </div>
          <Main/>
          <Footer/> 
      </div>
    

    );
  }
}

export default App;
